import ButtonDota from "@/components/utils/cards/ButtonDota";
import Image from "../../../../public/img/bg-home.png";
import { useRouter } from "next/router";
import { useTranslation } from "react-i18next";
import { HeroHighlight } from "./components/ui/hero-highlight";
import { RiArrowDownWideFill } from "react-icons/ri";
import { HeroParallax } from "./components/ui/hero-parallax";
import { Link } from 'react-scroll'
import { Vortex } from "./components/ui/vortex";
import AboutUs from "./AboutUs";
const Home = () => {
  const router = useRouter()
  const i18n = useTranslation()
  const products = [
    {
      title: "Basic",
      link: "/login",
      thumbnail:
        "/img/memberships/1-r.png",
    },
    {
      title: "Medium",
      link: "/login",
      thumbnail:
        "/img/memberships/2-r.png",
    },
    {
      title: "Advanced",
      link: "/login",
      thumbnail:
        "/img/memberships/3-r.png",
    },

    {
      title: "Pro",
      link: "/login",
      thumbnail:
        "/img/memberships/4-r.png",
    },

  ]

  return (
    <div>
      <div
        className="h-full  top-0 flex flex-col items-center"
        style={{
          backgroundSize: "cover",
          width: "100%",
          height: "100vh"
        }}
      >
        <div className="relative flex h-full w-full items-center justify-center">
          <Vortex>
            <div className="flex flex-col h-full justify-center items-center p-4 md:p-0 gap-4 relative">
              <img src="/img/logo-inverse.svg" className="w-72" alt="" />
              <span className="md:text-5xl text-2xl uppercase font-bold">{i18n.t('landing.sections.discover_your_potential')}</span>
              <div className="flex flex-col md:flex-row justify-center gap-4 mt-10">
                <ButtonDota onClick={() => router.push('/login')} rounded="lg" classNames="bg-white text-black font-bold text-xl uppercase">{i18n.t('landing.sections.join_dota')}</ButtonDota>
                <ButtonDota onClick={() => router.push('/login')} rounded="lg" classNames="text-xl uppercase">{i18n.t('landing.sections.login_to_my_account')}</ButtonDota>
              </div>

            </div>
          </Vortex>
        </div>
        <div className="absolute bottom-0 z-[99] animate-ping cursor-pointer">
          <Link smooth={true} duration={500} to="section-2" className="text-white text-[100px]" spy><RiArrowDownWideFill /> </Link>
        </div>
      </div>
      <div className="hidden md:flex transition delay-150 duration-300 ease-in-out" id="section-2">
        <HeroParallax
          products={products}
        />
      </div>
      <HeroHighlight>
        <AboutUs />
      </HeroHighlight>
    </div>
  );
};

export default Home;
